.vms-header-parent {
  header {
    background: #2F2E33;
    width: 100%;
    color: white;
    .vms-header-content {
      padding: 0 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      h3 {
        margin-left: 30px;
      }
      ul {
        list-style-type: none;
        padding: 0 10px;
        display: flex;
        align-items: center;
        margin: 10px;
        li {
          margin-left: 40px;
          display: inline-block;
          color: white;
          text-decoration: none;
          .MuiAvatar-root.MuiAvatar-circle {
            height: 34px;
            width: 34px;
            border: 2px solid #707070;
          }
        }
      }
      .vms-header-icon {
        font-size: 26px;
      }
      .vms-logo {
        img {
          height: 24px;
          width: auto;
        }
      }
      .vms-popper-menu {
        left: -30px!important;
        top: 5px!important;
        z-index: 1;
        #menu-list-grow {
          padding: 8px 8px;
          outline: none;
          display: block!important;
          .vms-dropdown-menu {
            display: flex;
            align-items: center;
            font-size: 13px;
            font-weight: bold;
            padding: 12px 0px;
            cursor: pointer;
            &:hover {
              color: #FF9306;
              svg {
                color: #FF9306;
                margin-right: 14px;
              }
            }
            svg {
              color: #333333;
              margin-right: 14px;
            }
          }
        }
      }
    }
  }
}