
.vms-stream-parent-container {
  .vms-stream {
    .vms-view-filter {
      position: sticky;
      top: 0px;
      z-index: 3;
      box-shadow: 0px 4px 20px #00000008;
      padding: 20px 0px;
      background: white;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .tile-number {
        display: flex;
        align-items: center;
        padding-left: 30px;
        label {
          font-size: 14px;
          font-weight: 600;
        }
        select {
          margin-left: 20px;
          padding: 5px 5px;
          width: 50px;
          border-radius: 3px;
        }
      }
      .tile-view-option {
        padding-right: 30px;
        img {
          cursor: pointer;
          height: 24px; width: 24px;
          margin: 0 0px 0 20px;
        }
      }
    }

    .vms-video-tile {
      padding: 30px;
    }

    .vms-stream-item-container {
      padding: 20px;
      .stream-item {
        padding: 10px;
      }
      .camera-name {
        margin: 10px;
        font-size: 16px;
        font-weight: 600;
      }
    }

    .list-view {
      padding-left: 30px;
      max-width: 800px;
      display: inline-block!important;
      width: 100%;
      .stream-item {
        display: flex;
        margin: 30px 0;
      }
      .camera-name {
        font-size: 18px;
        margin: 15px 15px 15px 30px;
        font-weight: 600;
      }
    }
  }
  .vms-player-container .actual-video {
    width: 100%!important;
    min-width: 460px;
    height: 34vh!important;
  }
}
