.vms-subnavigation {
  width: 100%;
  background: #707070;
  .vms-navigation-content {
    padding: 0 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .sub-menu-container {
      display: flex;
      .active {
        background: #2F2E33;
      }
      a {
        text-decoration: none;
      }
      .menu-item {
        font-weight: 300;
        color: white;
        display: flex;
        padding: 15px 15px;
        align-items: center;
        .sub-menu-icon {
          font-size: 26px;
        }
      }
    }
    .vms-search-area {
      background: #656565;
      border-radius: 4px;
      input {
        border: 0px!important;
      }
      .MuiOutlinedInput-notchedOutline {
        border: 0px;
      }
      .MuiOutlinedInput-adornedStart {
        height: 40px;
      }
      svg {
        color: white!important;
        font-size: 22px;
      }
      input {
        color: white;
        font-weight: 300;
        font-size: 14px;
      }
      ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
        color: white;
        font-weight: 300;
        opacity: 1; /* Firefox */
      }
    }
  }
}