html, body {
  font-family: 'Inter', sans-serif!important;
  color: #333333;
  background: #F5F5F5;
}

ul {
  list-style-type: none;
}

button {
  font-family: 'Montserrat', sans-serif!important;
  &.inter {
    font-family: 'Inter', sans-serif!important;
    font-weight: 600;
  }
}

.vms-label {
  font-weight: 600;
  font-size: 14px;
}

.vms-value {
  font-size: 14px;
}

/* Colors */
$primary: "#3A5199";

.vms-bg-primary {
  background-color: $primary!important;
}

.vms-shadow {
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.122);
}

.vms-container {
  padding: 30px;
}

.vms-table-title {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 700;
}

.vms-full-width {
  width: 100%;
}

.vms-custom-field {
  position: relative;
  .vms-field-icon-right {
    position: absolute;
    right: 0px;
    bottom: -10px;
    transform: translate(-50%, -50%);
  }
}

.vms-title {
  font-size: 32px;
}

.vms-subtitle {
  font-size: 20px;
}

.vms-text-center {
  text-align: center;
}

.vms-text-right {
  text-align: right;
}

.vms-text-left {
  text-align: left;
}

.vms-flex {
  display: flex;
}

.vms-flex-items-center {
  display: flex;
  align-items: center;
}

.vms-flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.vms-justify-center {
  justify-content: center;
}

.vms-mt-lg { margin-top: 40px!important; }
.vms-mt-md { margin-top: 24px!important; }
.vms-mt-sm { margin-top: 16px!important; }
.vms-mt-xs { margin-top: 8px!important; }

.vms-pt-lg { padding-top: 40px!important; }
.vms-pt-md { padding-top: 24px!important; }
.vms-pt-sm { padding-top: 16px!important; }
.vms-pt-xs { padding-top: 8px!important; }


.vms-mr-lg { margin-right: 40px!important; }
.vms-mr-md { margin-right: 24px!important; }
.vms-mr-sm { margin-right: 16px!important; }
.vms-mr-xs { margin-right: 8px!important;  }

.vms-ml-lg { margin-left: 40px!important; }
.vms-ml-md { margin-left: 24px!important; }
.vms-ml-sm { margin-left: 16px!important; }
.vms-ml-xs { margin-left: 8px!important;  }

.vms-mr-md { margin-right: 24px; }
.vms-mr-sm { margin-right: 16px; }

.vms-mb-lg { margin-bottom: 40px!important; }
.vms-mb-md { margin-bottom: 24px; }
.vms-mb-xs { margin-bottom: 8px; }

.vms-pb-md { padding-bottom: 24px!important; }
.vms-px-md { padding-right: 24px!important; padding-left: 24px!important; }

.vms-blue {
  color: #3A5199;
}

.vms-backdrop {
  z-index: 99999!important;
  background-color: rgba(0, 0, 0, 0.9)!important;
}

.vms-green {
  color: #47993A!important;
}

.vms-green-2 {
  color: #8DE67F!important;
}

.vms-yellow-2 {
  color: #cfd515!important;
}

.vms-red {
  color: #993A3D!important;
}

.vms-blue {
  color: #3A5199!important;
}

.vms-white {
  color: #ffffff;
}

.vms-bg-red {
  background-color: #993A3D!important;
  background: #993A3D!important;
}

.vms-bg-blue {
  background-color: #3A5199!important;
  background: #3A5199!important;
}

.btn-blue {
  background-color: #3A5199!important;
  background: #3A5199!important;
  color: #ffffff!important;
}

.btn-yellow {
  background-color: #FF9306!important;
  background: #FF9306!important;
  color: #ffffff!important;
}


.vms-yellow {
  color: #FF9306;
}

.vms-text-white {
  color: white!important;
}

.vms-dialog {
  .MuiDialog-paper {
    width: 100%;
    max-width: 580px;
  }
  .MuiPaper-rounded {
    border-radius: 8px!important;
  }
}

.vms-dialog-header {
  padding: 24px 24px 0px 24px;
}

.vms-dialog-title {
  font-weight: 700;
  font-size: 20px;
}

.vms-video-backdrop-container {
  position: relative;
  .vms-player-container {
    width: 720px;
    height: 420px;
  }
  text-align: center;
  h3 {
    color: #ffffff;
    font-size: 24px;
    font-weight: 500;
  }
  svg {
    position: absolute;
    right: -32px;
    color: white;
    cursor: pointer;
  }
  svg.action {
    position: unset!important;
    right: unset!important;
    cursor: pointer;
  }
}

.MuiBackdrop-root {
  background-color: rgba(0, 0, 0, 0.8)!important;
}

.vms-auto-container {
  width: 300px;
  margin: 0 auto!important;
}

label {
  span.MuiCheckbox-colorPrimary {
    padding: 5px!important;
  }
}

.dynamic-div {
  max-width: 200px;
  margin-top: 12px;
  .item-selected{
    font-size: 14px;
  }
  button {
    height: 16px;
    width: 16px;
    padding: 3px;
    color: white;
  }
}

.bg-red {
  background: tomato!important;
}