
.vms-record-parent-container {
  .vms-record {
    .vms-view-filter {
      position: sticky;
      top: 0px;
      z-index: 3;
      box-shadow: 0px 4px 20px #00000008;
      padding: 20px 0px;
      background: white;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .tile-number {
        display: flex;
        align-items: center;
        padding-left: 30px;
        label {
          font-size: 14px;
          font-weight: 600;
        }
        select {
          margin-left: 20px;
          padding: 5px 5px;
          width: 50px;
          border-radius: 3px;
        }
      }
      .tile-view-option {
        padding-right: 30px;
        img {
          cursor: pointer;
          height: 24px; width: 24px;
          margin: 0 0px 0 20px;
        }
      }
    }

    .vms-video-tile {
      padding: 30px;
    }

    .vms-record-item-container {
      padding: 20px;
      .record-item {
        padding: 10px;
      }
      .record-name {
        margin: 10px;
        font-size: 20px;
        font-weight: 700;
      }
      .dateTime {
        font-weight: 600;
        font-size: 16px;
        color: grey;
        margin: 14px 0;
      }
      .status {
        font-weight: 700;
        text-transform: uppercase;
      }
    }

    .list-view {
      padding-left: 30px;
      max-width: 100%;
      display: inline-block!important;
      width: 100%;
      .side-info {
        margin: 20px;
      }
      .record-item {
        display: flex;
        margin: 30px 0;
      }
      .record-name {
        margin: 10px;
        font-size: 20px;
        font-weight: 700;
      }
      .dateTime {
        margin: 20px 10px!important;
        font-weight: 600;
        font-size: 14px;
        color: grey;
        margin: 14px 0;
      }
      .status {
        margin: 10px;
        font-weight: 700;
        text-transform: uppercase;
      }
      .vms-player-container .actual-video {
        width: 100%!important;
        min-width: 400px;
        height: 30vh!important;
      }
      .vms-player-container .actual-video {
        width: 100%!important;
        min-width: 460px;
        height: 30vh!important;
      }
    }
  }
  .vms-player-container .actual-video {
    width: 100%!important;
    min-width: 460px;
    height: 34vh!important;
  }
}

.record-action-menu {
  &.active {
    right: -300px;
    transition: all ease-out 100ms;
  }
  transition: all ease-in 100ms;
  position: fixed;
  text-align: center;
  z-index: 2;
  top: 180px;
  right: 0px;
  padding: 12px 30px;
  border-radius: 8px;
  background:#d5d6d2;
  .record-btn-container {
    padding: 10px;
    position: relative;
    .btn-trigger {
      cursor: pointer;
      position: absolute;
      left: -82px;;
      width: 52px;
      padding: 16px 0;
      background: #2e2d32;
      color: white;
      border: 0px;
      border-top-left-radius: 8px;
      border-bottom-left-radius: 8px;
      outline: none;
      &:hover {
        svg {
          transform: rotate(360deg);
          transition: ease 200ms;
        }
      }
    }
    button {
      display: block;
      padding: 14px 10px;
      width: 220px;
      margin: 30px 0;
    }
  }
}

.vms-sticky {
  position: sticky;
  top: 80px;
}

.ml-30 {
  margin-left: 30px;
}

.pl-30 {
  padding-left: 30px;
}

.px-30 {
  padding-left: 30px;
  padding-right: 30px;
}


.action-remove {
  button {
    width: 160px;
    margin-left: 8px;
  }
}