.vms-auth {
  background: url('../assets/img/auth-bg.jpg');
  background-position: center;
  background-size: cover;
  height: 100vh;
  width: 100%;
  a {
    text-decoration: none;
    color: #D5D6D2;
    font-weight: 400;
    font-size: 16px;
  }
  .vms-auth-overlay {
    width: 100%;
    max-width: 400px;
    position: absolute;
    padding: 20px 40px 50px 40px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.527);
    border-radius: 6px;
    .vms-title {
      color: white;
      margin: 20px 0 30px 0;
    }
    .vms-subtitle {
      color: white;
      margin: 20px 0 30px 0;
    }
    .vms-auth-btn {
      padding: 10px 0;
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 20px;
    }
    .vms-line {
      height: 1px;
      width: 100%;
      background: white;
    }
    .vms-field {
      background: #707070;
      border-radius: 3px!important;
      input {
        padding: 16px 20px;
        color: white!important;
      }
    }
    &.vms-forgot {
     .vms-short-description {
       color: white;
       font-weight: 100;
     } 
    }
  }
}