.vms-dashboard {
  max-width: 1024px;
  margin: 40px auto;
  .vms-tile-menu {
    a {
      text-decoration: none;
    }
    margin-top: 30px;
    .vms-tile {
      cursor: pointer;
      height: 200px;
      box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.122);
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      .tile-container {
        text-align: center;
        .vms-dashboard-icon {
          color: #3A5199;
          font-size: 36px;
        }
        h3 {
          margin-top: 26px;
          color: #3A5199;
          font-size: 16px;
        }
        .dashboard-setting-trigger {
          position: absolute;
          z-index: 2;
          top: 0px;
          height: 100%;
          width: 100%;
          left: 0px;
          opacity: 0;
          .vms-dropdown-menu {
            position: absolute;
            width: 100%; height: 100%;
          }
        }
      }
      &:hover {
        .vms-dashboard-icon, h3 {
          color: #333333;
        }
        box-shadow: none;
        background: #D5D6D2;
      }
    }
  }
}

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
  .MuiGrid-container {
    width: 100%!important;
    margin: auto!important;
  }
}