.vms-table {
  padding: 30px;
}

.table {
  min-width: 750;
}

.paper {
  width: 100%;
}
.visuallyHidden {
  border: 0;
  clip: 'rect(0 0 0 0)';
  height: 1;
  margin: -1;
  overflow: 'hidden';
  padding: 0;
  position: 'absolute';
  top: 20;
  width: 1;
}

.MuiTableCell-root {
  padding: 10px!important;
}

.MuiTableRow-root.Mui-selected {
  background-color: white!important;
}

.vms-table-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  margin-top: 10px;
}